import Profilepic from './images/profile.jpg';
import './App.css';
import { useState } from 'react'
import Social from './components/social';
import { library } from '@fortawesome/fontawesome-svg-core';
import Bio from './components/bio';
import Likes from './components/likes';


function App() {
  const [skills, setSkills] = useState("My name is Kish")

  const handlename = () => {
    setSkills("My name is Kish")
  }

  const handleskill = () => {
    setSkills("I'm a Frontend Developer")
  }
  
  const [count, setCount] = useState(0);
 
  const handlecount = ()=>{
    setCount(count+ 1)
  }

  return (
    <>
      <div className="profile-card">
        <main className="card">
          <div className="welcome">
            <img src={Profilepic} className="pic" alt="logo" />
            <h1>Hi! </h1>
            <Bio info={skills} />
          </div>

          <div className="btns">
            <button onClick={handlename}> Name</button>
            <button onClick={handleskill}> Skills</button>
          </div>

          <div className ="likeReaction" >
            <Likes counter={count}/>
            <button onClick={handlecount} className='like' >
              <i class='fas fa-heart'size="2x"></i>
            </button>
          </div>

          <Social className="social" />

        </main>
      </div>
    </>
  );
}

export default App;
